
import { Vue, Options } from 'vue-class-component'
import ref from 'vue'

@Options({})
export default class Sidebar extends Vue {
  /*
        opens the sidebar
     */
  public open () {
    this.$store.commit('setSidebarOpened', true)
  }

  /**
     * Closes the Sidebar
     */
  public close () {
    this.$store.commit('setSidebarOpened', false)
  }

  /**
     * Toggles the Sidebar from its current state to the opposite
     */
  public toggle () {
    this.$store.commit('toggleSidebar')
  }

  get sidebarIsOpened (): boolean {
    return this.$store.state.sidebarOpened
  }

  /**
     * getter for the modal class containing the Sidebar
     */
  public get modalClass () {
    return this.sidebarIsOpened ? 'modal opened' : 'modal closed'
  }
}
