
import { Vue, Options } from 'vue-class-component'

@Options({
  props: {
    tagTxt: ''
  }
})
export default class Tag extends Vue {
    /**
     * Text to display for this tag
     */
    tagTxt!: string
    /**
     * getter for the link to 'articles' with this tag
     */
    get link (): string {
      return `/?tag=${this.tagTxt}`
    }
}
